.footer {
  width: 100%;
  height: 40px;
  background-color: teal;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer > h1 {
  font-size: 18px;
  color: white;
}

@media only screen and (min-width: 300px) and (max-width: 580px) {
  .footer > h1 {
    font-size: 9px;
  }
}
